                      مشكلة فى نطق هذا الصوت , و هى :- /n أن تكون ( ر ) خلفية أى
                      تحدث ذبذبة فى النصف الخلفى للسان ( مؤخرة اللسان ) . و
                      السؤال الان :- كيف نعالج ( ر ) الخلفية ؟؟؟ 1- عن طريق
                      التهيئة السابقة . 2- أو الملاحظة أمام المرأة . 3- يأخذ
                      المدرس قصاصة ورق و يضعها أمام فمه و يصدر صوت الراء … و على
                      الطفل ملاحظة أن القصاصة تتحرك وفقاً لتذبذب اللسان . 4-
                      محاولة أصدار صوت ( تر- تر – تر ) . 5- محاولة المدرس تحريك
                      لسان الطفل بأداة قد يعطى نتيجة لا بأس بها , و لكى يجب أن
                      تكون معقمة . و السؤال الان :- متى يصعب معالجة ( ر )
                      الخلفية ؟؟ فى حالتين :- ( الاولى ) أذا كان الموضوع( العطل
                      ) طبيعياً , أى أن الطفل لا يستطيع رفع لسانة الى الاعلى
                      كأمر طبيعى مما يعيق حركة الارتجاج … و هذا نتيجة لقصر فى
                      العصب ( العرق ) الذى يربط أسفل اللسان .. هنا فقط نعرض
                      الطفل على طبيب مختص . ( الثانية ) أذا كانت ظاهرة وراثية
                      يصعب تخطيها . 1- تحويل او نطق صوت حرف الراء (ر) الى ياء(ي)
                      اوالى واو(و) 2- تحويل او نطق حرف الراء (ر) الى لام (ل) 3-
                      تحويل او نطق حرف الراء (ر) الى غين (غ) والتى تعانى منها هى
                      النوع الأول أولا :يجب التاكد من عدم وجود عائق يمنع اللسان
                      من التحرك بطريقه طبيعيه مثل رابط اللسان الذى يعوق اللسان
                      من الحركه لأعلى فلابد إن وجد مثل هذه المشكه الخلقيه اجرء
                      تدخل جراحى اولاً ثانيا :وهى المرحله الهامه فى العلاج هى
                      تحويل نطق من (ر) إلى (ل) وذلك من خلال التدريب امام المرأة
                      على رفع اللسان لأعلى حيث فى نطق حرف اللام لابد من ان يطرف
                      طرف اللسان سقف الحلق الصلب عند منابت الأسنان العليا ويكون
                      ذلك من خلال التدريب علي نطق حرف اللام لفترة ما حتى يتم
                      التثبيت عليها من خلال نطقها فى كلمات تبدأ بحرف اللام
                      (لمياء) ثم بكلمات فى منتصفها حرف اللام ( سلم) ثم بكلمات
                      تنتهى بحرف اللام ( معلم ) ثم يتم التبيت من خلال جمل تحتوى
                      على كلمات بها حرف اللام وهكذا ويتم التأكيد على هذه
                      التدريبات من خلال تحليل محتوى الكلام العادى او الأرتجال من
                      خلال حوار عادى او مناقشه بينك وبين شخص اخر والتأكد من نطق
                      حرف الراء —–لام هذا وبعد ان تم تحويل اللدغه الرائيه الى
                      نطقها لام بدلا من ياء يتم هنا فى هذه المرحله نطقها بطريقه
                      صحيحه وهى حرف الراء وهنا تكون المشكله اصبحت بسيطه للغايه
                      فمن خلال التدريب امام المرأة ومحاوله معرفه مخرج حرف الراء
                      والكيفيه الصحيحه لنطقه وهى ان حرف الراء من الحروف التى
                      يكون ملامستها لمنابت الأسنان العليا وعند نهايتها متكرر
                      فحاول نطق ( إرررررررررر) فمع كسر حرف الألف ثم تكرار حرف
                      الراء وأطالته لأكبر مدة ممكنه يكمن نطقه بطريقه سليمه إذا
                      لم تتمكن فحاول بكلمه مثل ( دجرررررررا ) او كلمه ( زرررررر
                      ) والمقصود هنا بنطق تلك الكلمات هو الشعور بالأهتزاز الزى
                      يحدثه نطق الأحرف الأولى من تلك الكلمات مثل ( ج ) فى الكلمه
                      الولى وحرف ال ( ز ) فى الكلمه الثانيه /n بعد التمرين على
                      نطق تلك الكلمات مثل ( دجرا ) والشعور بهذا الأهتزاز الذى
                      يساعد على نطق حرف الراء وتحرك اللسان وبعد التأكد من نطقه
                      فيجب هنا التثبيت من خلال نطق الحرف فى كلمات تبدأ واخرى
                      تنتهى بحرف الراء وكلمات اخرى فى منتصفها حرف الراء……….
                      ……هذا فى غايه الأهميه ثم بعد ذلك من خلال جمل تحوى كلمات
                      بها حرف الراء وهكذا…ثم يتم التأكد من نطقها من خلال الحوار
                      العادى مع المحيطين بك وملاحظه طريقه نطقك لحرف الراء
                      ملاحظات هامه اولا – قد لا يسطيع الفرد القيام بهذه
                      التدريبات بمفرده ولكن يفضل ان تكون مع اخصائى تخاطب متمرس
                      ثانيا- فترة التدريب قد لا تستمر اكثر من 12 جلسه ثالثا-
                      التدريب لابد ان يكون امام مرآه ليسهل ملاحظه طريقه النطق
                      عمل تمرين بسيط برفع طرف اللسان العلوي إلى اللثة العليا
                      التي تقع بعد الأسنان مباشرة، وتكرار الحركة والصوت ررررررر.
                      ويمكن لبعض الأشخاص أن يستفيدوا من تكرار الصوت في جملة بها
                      صوت الراء في جميع الكلمات، مثل: رمى رامي الكرة في المرمي
                      (10 مرات)
                    </span>
